export default {
  props: ['value'],
  data() {
    return {}
  },
  computed: {
    modalOpen: {
      get: function () {
        return this.value
      },
      set: function (nv) {
        this.$emit('input', nv)
      },
    },
  },
  methods: {
    githubLogin() {
      this.$auth.loginWith('github').then((e) => {
        console.log(e)
      })
    },
    googleLogin() {
      this.$auth.loginWith('google')
    },
    close() {
      this.$emit('input', false)
    },
  },
}
